import { Canvas } from "@react-three/fiber";
import { useLoader } from "@react-three/fiber";
import {Environment, OrbitControls, Stats, TransformControls} from "@react-three/drei";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { Suspense } from "react";
import { Html, useProgress } from '@react-three/drei'
import './App.css';

function Loader() {
  const { progress } = useProgress()
  return <Html center>{progress} % loaded</Html>
}

const Model = () => {
  const gltf = useLoader(GLTFLoader, "./wood-table-3d-model/wood_table_001_4k.gltf");
  return (
    <>
      <primitive object={gltf.scene} scale={1} />
    </>
  );
};

const ModelEnotGlb = () => {
  const gltf = useLoader(GLTFLoader, "./Enotproject7.glb");
  return (
    <>
      <primitive object={gltf.scene} scale={1} />
    </>
  );
};
const Kenzo = () => {
  const gltf = useLoader(GLTFLoader, "./kenzo2.glb");
  return (
    <>
      <primitive object={gltf.scene} scale={1} />
    </>
  );
};
const Girl = () => {
  const gltf = useLoader(GLTFLoader, "./hit2_female_swordsman.glb");
  return (
    <>
      <primitive object={gltf.scene} scale={1}  position={[0, 0, 0]} />
    </>
  );
};

export default function App() {
  return (
    <>
      <div className="App">
        <Canvas>
          <Suspense fallback={<Loader />}>
            <Model />
            <TransformControls />
            <OrbitControls />
            <directionalLight position={[3.3, 1.0, 4.4]} intensity={4} />
            <Environment files="./widen-1680-noupsize.hdr" background />
          </Suspense>
        </Canvas>
        <Canvas>
          <Suspense fallback={<Loader />}>
            <ModelEnotGlb />
            <TransformControls />
            <OrbitControls target={[0, 0, 0]} />
            <Environment preset="sunset" background />
          </Suspense>
        </Canvas>
      </div>
      <div className="App">
        <Canvas>
          <Suspense fallback={<Loader />}>
            <Kenzo />
            <OrbitControls />
            <Environment preset={['apartment']} background />
          </Suspense>
        </Canvas>
        <Canvas camera={{ position: [-0.5, 1, 2] }}>
          <Suspense fallback={<Loader />}>
            <Girl />
            <OrbitControls target={[0, 1, 0]} autoRotate />
            <Environment preset="park" background />
            <Stats showPanel="{2}" />
          </Suspense>
        </Canvas>
      </div>
    </>
  );
}
